import React, { Component } from 'react';
import { Query } from 'react-apollo';
import gql from 'graphql-tag';
import User from './User';
import { Title } from './Item';
import Reviews from './Reviews';
import { Footnote } from './Signup';

const ITEMS_FOR_USER_QUERY = gql`
	query ITEMS_FOR_USER_QUERY($userId: String) {
		items(where: { user: { id: $userId } }) {
			id
			title
			description
			image
			maxDays
		}
	}
`;

class Account extends Component {
	render() {
		return (
			<>
				<User>
					{({ data: { currentUser } }) => {
						console.log(currentUser);
						return (
							<Query
								query={ITEMS_FOR_USER_QUERY}
								variables={{ user: { id: currentUser.id } }}
							>
								{({ data, loading, error }) => {
									console.log(data);
									return <Title>{currentUser.name}</Title>;
								}}
							</Query>
						);
					}}
				</User>
				<Footnote>
					{/* this looks strange but is for styling purpose */}
					<button>
						<span className="bold">Sign Out üëãüèº</span>
					</button>
				</Footnote>
			</>
		);
	}
}

export default Account;
